// Components :: Color Palette

$border-radius-color-palette: 6px;

.c-color-swatch {
  margin-bottom: 30px;
  transform: translateZ(0);

  & > *:first-child {
    border-top-left-radius: $border-radius-color-palette;
    border-top-right-radius: $border-radius-color-palette;
  }

  & > *:last-child {
    border-bottom-left-radius: $border-radius-color-palette;
    border-bottom-right-radius: $border-radius-color-palette;
  }
}

.c-color-swatch__item {
  cursor: pointer;
  line-height: 1;
  padding: 20px;
  position: relative;
  transition: box-shadow 0.15s ease;
  transform: scale(1);

  &:before {
    background-color: rgba(white, 0);
    bottom: 0;
    content: '';
    left: 0;
    position: absolute;
    right: 0;
    transition: background-color 0.6s ease;
    top: 0;
    z-index: 0;
  }

  &:hover {
    border-radius: 3px;
    box-shadow: 0 2px 4px rgba(black, 0.08), 0 12px 20px rgba(black, 0.12);
    transform: scale(1.05);
    z-index: 2;
  }
}

.c-color-swatch__content {
  position: relative;
  z-index: 1;
}

// Modifiers
.c-color-swatch__item--lg {
  box-shadow: 0 1px 2px rgba(black, 0.20), 0 -1px 0 rgba(black, 0.10) inset;
  z-index: 1;
}

.c-color-swatch__item--dark {
  color: white;

  strong {
    color: white;
  }
}


// Actions
.c-color-swatch__item:active {
  &:before {
    background-color: rgba(white, 0.3);
    transition: background-color 0.2s ease;
  }
}
