// Components :: Overlay

.c-overlay {
  bottom: 0;
  display: none;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 55;
}

.c-overlay__content {
  font-size: 48px;
  font-weight: 600;
  left: 0;
  letter-spacing: -0.05rem;
  position: absolute;
  right: 0;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
}

.c-overlay__background {
  bottom: 0;
  left: 0;
  opacity: 0.75;
  position: absolute;
  right: 0;
  top: 0;
}

// Modifiers
.c-overlay--dark {
  color: white !important;
}
