// Layouts :: Styleguide

.hs-styleguide {
  padding: 0;

  @include breakpoint(md) {
    padding: 15px 0;
  }
}

.hs-container {
  margin-top: 10px;

  @include breakpoint(md) {
    margin-left: 220px;
    margin-top: 0;
    padding: 0 15px;
  }

  @include breakpoint(lg) {
    margin-left: 280px;
  }
}
