// Layouts :: Styleguide (Nav)

.hs-nav-side {
  background-color: white;
  display: flex;
  flex-direction: column;
  width: 100%;
  z-index: 3;

  @include breakpoint(md) {
    bottom: 0;
    position: fixed;
    top: 0;
    width: 220px;
  }
  @include breakpoint(lg) {
    width: 280px;
  }

  &__header {
    position: relative;
  }

  &__toggle {
    filter: blur(0);
    padding: 10px;
    position: absolute;
    left: 5px;
    transform: translateY(-50%);
    top: 50%;
    z-index: 3;

    @include breakpoint(md) {
      display: none;
    }
  }

  &__body {
    display: none;
    flex: 1;
    position: relative;

    @include breakpoint(md) {
      display: block;
    }
  }

  &__content {
    @include breakpoint(md) {
      bottom: 0;
      overflow: auto;
      position: absolute;
      top: 0;
      width: 100%;
    }

    a {
      display: block;
      padding: 3px 0;
    }
  }

  ul ul {
    padding-left: 15px;
  }
}
