// Sections :: iOS

.s-hs-docs--ios {

  img {
    border: 1px solid _color(grey, 600);
    border-radius: 3px;
    box-shadow: 0 1px 7px 0 rgba(0,0,0,.08);
    max-width: 320px;
  }

  figcaption span {
    position: relative;
    display: block;
  }

  .icon {
    position: absolute;
    left: -29px;
    top: 0;
  }

}
