// Component :: Branding

.c-block--branding {
  background-color: _color(blue, 500);
  background-image: url('/images/styleguide/customer-bg.png');
  border-radius: 0 !important;
  color: white;
  display: block;
  padding: 10px;

  @include breakpoint(md) {
    height: 140px;
    padding: 20px;
  }

  .logo-svg {
    height: auto;
    max-width: 100%;
    width: 120px;

    @include breakpoint(md) {
      width: 140px;
    }
  }

  a {
    color: white;
  }
}
