// Layouts :: Content

.s-hs-docs {
  & > h1,
  & > h2 {
    font-weight: 400;
  }

  & > h3,
  & > h4 {
    font-weight: 600;
  }

  & > h2,
  & > h3,
  & > h4,
  & > h5 {
    margin-top: 52px;
  }

  & > h1,
  & > h2,
  & > h3,
  & > h4,
  & > h5 {
    color: black;
    line-height: 1.2;
    margin-bottom: 12px;
  }

  & > h2 + h3,
  & > h3 + h4 {
    margin-top: 16px;
  }

  .hs-content__top + p {
    font-size: 130%;
    font-weight: 400;
    letter-spacing: -0.01rem;
  }

  & > p {
    color: _color(charcoal, 500);
    line-height: 1.5;
    margin-bottom: 1.5em;

    + .highlighter-rouge {
      margin-top: -0.5em;
    }
  }

  & > ul,
  & > ol {
    color: _color(charcoal, 500);
  }

  hr {
    margin: 60px 0;
  }

  table {
    font-size: 1rem;
  }

  // Kramdown table of contents
  @at-root {
    // Unfortunately, there isn't any way to use class names instead of
    // #id for Kramdown's auto-generated table of contents component
    // Hide table of contents header
    #contents {
      color: _color(charcoal, 200);
      font-size: 0.9rem;
      font-weight: bold;
      margin-top: 20px;
      text-transform: uppercase;
    }

    #markdown-toc {
      margin-bottom: 32px;

      > li:first-child {
        display: none;
      }
    }
  }
}
