// Sections :: Scouticons

.s-hs-docs--scouticons {
  .c-icon-list {
    position: relative;

    &__item {
      display: inline-block;
      margin-bottom: 10px;
      padding: 15px 10px;
      text-align: center;
      vertical-align: top;
      width: 24%;

      @include breakpoint(sm) {
        margin-bottom: 15px;
        width: 100px;
      }

      .icon {
        font-size: 24px;
      }

      span {
        color: _color(charcoal, 300);
        display: block;
        font-size: 0.8rem;
        margin-top: 10px;
      }
    }
  }
}
